import React from 'react';
import ReactDOM from 'react-dom';
import './css/world.css';
import queryString from 'query-string';
import SearchCom from './components/RouterCom';
import { BrowserRouter as Router, Route, Link, Redirect, NavLink, Switch, withRouter } from 'react-router-dom';

import fetchJson from './data/dataList';
import Nav from './components/Nav';

class Main extends React.Component {
	render() {
		return (
			<div id="main">
				<Nav></Nav>
				<Router>
        			<div className="position-relative mybanner" style={{ height: '20rem', margin: '0px', width: '100%' }}>
						<div className="position-absolute top-50 start-0 " style={{ paddingLeft: '20%' }}>
							{/* <Link style={{width: '10rem', height:'5.5rem',fontSize:'2.25em'}} className='btn btn-lg btn-outline-dark top-50 rounded-pill' to='/covid-track'>Track</Link> */}
							<Link className="btn btn-lg btn-outline-dark top-50 rounded-pill" to="/covid-track">
								Track
							</Link>
						</div>
						<div className="position-absolute top-50 end-0 " style={{ paddingRight: '20%' }}>
							<Link style={{ width: '7rem', height: '3rem' }} className="btn btn-lg btn-outline-dark top-50 rounded-pill" to="/search">
								Search
							</Link>{' '}
						</div>
					</div>
					<hr></hr>
					<Switch>
						<Route path="/display" exact component={Display}></Route>
						<Route path="/covid-track/" exact component={WorldMap}></Route>
						<Route path="/search/" exact component={SearchCom}></Route>
					</Switch>
				</Router>
			</div>
		);
	}
}
function Display(props) {
  let data = props.history.location.state.jobInfo
//   console.log(data)
	return (
		<div>
			<div dangerouslySetInnerHTML={{ __html: data }}></div>
		</div>
	);
}

class WorldMap extends React.Component {
	render() {
		return (
			<div>
				{/* <div className='container'> */}
				<div className="col-12 col-4 " id="map"></div>
			</div>
		);
	}

	clickEvent = () => {
		this.props.history.goBack();
	};

	async componentDidMount() {
		let dataArray = null;
		await fetchJson().then((res) => {
			dataArray = res;
		});
		// console.log(dataArray)
		var myChart = window.echarts.init(document.getElementById('map'));
		var option = {
			title: {
				text: 'COVID-19 CORONAVIRUS CONFIRMED',
				subtext: 'World Covid-19 Tracker from BBC',
				sublink: 'https://www.bbc.com/news/world-51235105',
				left: 'center',
				top: 'top',
			},
			tooltip: {
				trigger: 'item',
				formatter: function (params) {
					var value = (params.value + '').split('.');
					value = value[0].replace(/(\d{1,3})(?=(?:\d{3})+(?!\d))/g, '$1,') + '.' + value[1];
					return params.seriesName + '<br/>' + params.name + ' : ' + value;
				},
			},
			toolbox: {
				show: true,
				orient: 'vertical',
				left: 'right',
				top: 'center',
				feature: {
					mark: { show: true },
					dataView: { show: true, readOnly: false },
					restore: { show: true },
					saveAsImage: { show: true },
				},
			},
			visualMap: {
				type: 'continuous',
				min: 0,
				max: 1000000,
				left: 'left',
				top: 'center',
				text: ['High', 'Low'],
				realtime: false,
				calculable: true,
				color: ['orangered', 'yellow', 'lightskyblue'],
			},
			series: [
				{
					name: 'Covid-19 ONFIRMED',
					type: 'map',
					mapType: 'world',
					roam: true,
					itemStyle: {
						emphasis: { label: { show: true } },
					},
					data: dataArray,
				},
			],
		};

		myChart.setOption(option);
	}
}

ReactDOM.render(<Main />, document.getElementById('root'));
