import dataArray from './index';

function toDecimal2(x) {
	let f = parseFloat(x);
	if (isNaN(f)) {
		return false;
	}
	f = Math.round(x * 100) / 100;
	let s = f.toString();
	let rs = s.indexOf('.');
	if (rs < 0) {
		rs = s.length;
		s += '.';
	}
	while (s.length <= rs + 2) {
		s += '0';
	}
	return s;
}

const fetchData = () => {
	return new Promise((resolve, reject) => {
		fetch('https://news.files.bbci.co.uk/include/newsspec/26866-coronavirus-mapped/assets/app-project-assets/data/data_latest.json')
			.then((res) => res.json())
			.then((res) => {
				resolve(res);
			})
			.catch((err) => {
				return reject(err);
			});
	});
};
async function fetchJson() {
	let jsonData = await fetchData();
	console.log(jsonData);
	let dataList = {};
	dataArray.forEach((item, i) => {
		dataList[item.name.toLowerCase()] = item.value;
	});

	let tmp = jsonData.regions;

	Object.entries(tmp).forEach((item) => {
		let current = item[1].countries;
		for (let key in current) {
			dataList[current[key].country_region] = toDecimal2(current[key].confirmed);
		}
	});

	for (let item of dataArray) {
		item.value = dataList[item.name.toLowerCase()];
		if (item.name === 'United States') {
			item.value = dataList['us'];
			// console.log(item.value)
		}
	}

	console.log(dataArray);
	return dataArray;
}

export default fetchJson;
