import React from 'react';

import Pagination from './Pagination';
import '../css/search.css';
const log = console.log;
export default class SearchCom extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			location: '',
			keyWord: '',
			data: {},
			rows: [],
			pageTotal: 0,
			totalHits: 0,
			success: false,
			page: 1,
			rate: '',
		};
	}

	onSubmit = (e) => {
		if (e != null) {
			e.preventDefault();
		}
		const data = {
			location: this.state.location,
			keyWord: this.state.keyWord,
			rate: this.state.rate,
			page: this.state.page,
		};

		fetch('http://localhost:8080/myElastic/search', {
			method: 'POST',
			body: JSON.stringify(data),
			mode: 'cors',
			headers: new Headers({ 'Content-Type': 'application/json' }),
		})
			.then((res) => {
				console.log(res);
				return res.json();
			})
			.then((returnedData) => {
				var result = returnedData;
				if (result.success !== 'no') {
					let iterData = result.rows;
					let pageInfo = result.pageTotal;
					this.setState({
						location: data.location,
						keyWord: data.keyWord,
						rate: data.rate,
						pageTotal: pageInfo,
						rows: iterData,
						success: true,
						totalHits: result.totalHits,
					});
				} else {
					this.setState({
						location: data.location,
						keyWord: data.keyWord,
						rate: data.rate,
						success: false,
					});
				}
				// console.log(result)
				// console.log('ÂêéÂè∞Ëé∑ÂæóÊï∞ÊçÆ')
			});
	};

	onChangeEvent = (e) => {
		if (e.target.name === 'location') {
			this.setState({
				location: e.target.value,
				page: 1,
			});
		} else if (e.target.name === 'keyWord') {
			this.setState({
				keyWord: e.target.value,
				page: 1,
			});
		} else if (e.target.name === 'rate') {
			console.log(e.target.value);
			this.setState({
				rate: e.target.value,
				page: 1,
			});
		} else {
			this.setState({
				page: e.target.value,
			});
		}
	};

	changePageNum = (pageNum) => {
		this.setState({
			page: pageNum,
		});
		setTimeout(this.onSubmit, 1);
	};

	render() {
		return (
			<div>
				<div>
					<form onSubmit={this.onSubmit}>
						<div className="form-row" id="searchbox">
							<div className="form-check form-check-inline col-2" style={{ marginLeft: '10%' }}>
								{/* <label htmlFor="inputKeyWord" className="form-label">KeyWordÔºö</label> */}
								<input
									type="text"
									id="inputKeyWord"
									className="form-text form-control"
									name="keyWord"
									value={this.state.keyWord}
									onChange={this.onChangeEvent}
									placeholder="please inter keyword"
								></input>
							</div>

							<div className="form-check form-check-inline col-3">
								<input
									type="text"
									name="location"
									aria-describedby="basic-addon1"
									className="form-text form-control"
									value={this.state.location}
									onChange={this.onChangeEvent}
									placeholder="eg : ottawa"
								></input>
							</div>
							<div className="form-check form-check-inline col-3">
								<select defaultValue="0" name="rate" className="form-select form-select-sm" onChange={this.onChangeEvent}>
									<option value="4.5">{' >  4. 5 '}</option>
									<option value="4">{' 4 ~ 4.5 '}</option>
									<option value="3">{' 3 ~ 4 '}</option>
									<option value="2">{' < 3 '}</option>
									<option value="0">{' select one rate ‚≠ê'}</option>
								</select>
							</div>
							<input type="submit" value="go" className="form-check form-check-inline col btn-outline-dark btn-sm"></input>
						</div>
					</form>
				</div>
				<hr />
				<div className="dataTable" style={{ paddingLeft: '5%', paddingRight: '5%' }}>
					<IterData data={this.state.rows} self={this.props} success={this.state.success} />
				</div>
				{/* <hr></hr> */}
				<br></br>
				<div className="footer">
					<article>
						<Pagination
							config={{
								totalPage: this.state.pageTotal,
							}}
							changePage={this.changePageNum}
						></Pagination>
					</article>
				</div>
			</div>
		);
	}
}

function IterData(props) {
	let datas = [];
	const newpage = (element) => {
        // log(props)
		props.self.history.push('display', { jobInfo: element.jobInfo  });
	};

	props.data.forEach((element, i) => {

		datas.push(
			<tr key={i + 1}>
				<td>{element.companyName}</td>
				<td>{element.jobAddr}</td>
				<td>{element.jobTitle}</td>
				<td>{element.rate}</td>
				<td>
					<a href={element.url} target="_blank">
						<span role="img" aria-label="link">
							üîó
						</span>
						Link
					</a>
				</td>
				<td>
					<button onClick={()=>{newpage(element)}}>Detail</button>
				</td>
			</tr>
		);
	});

	if (props.success === true) {
		return (
			<table className="table table-striped table-hover">
				<thead className="table-primary" key={0}>
					<tr>
						<th scope="col">CompanyName</th>
						<th scope="col">Place</th>
						<th scope="col">JobTitle</th>
						<th scope="col">Rate</th>
						<th scope="col">URL</th>
						<th scope='col'>Match Detail</th>
					</tr>
				</thead>
				<tbody>{datas}</tbody>
			</table>
		);
	} else {
		return (
			<div style={{ textAlign: 'center' }}>
				<font color="red">no data</font>
			</div>
		);
	}
}
