import React,{ Component } from "react";
import style from "../css/page.scss";

export default class Pagination extends Component{
    constructor(props){
        super(props)

        this.state = {
            pageCurr:1,
            groupCount:7,
            startPage:1,
        }
    }

    create(){
        const {
            totalPage,
        } = this.props.config;

        const {
            pageCurr,
            groupCount,
            startPage
        } = this.state;

        let pages = [];

        if( totalPage <= 10){
            pages.push(<li onClick = { this.goPrev.bind(this) } className = { this.state.pageCurr === 1? 'nomore':"" } key={0}>‰∏ä‰∏ÄÈ°µ</li>)
            for(let i = 1;i <= totalPage; i++){
                // ÁÇπÂáªÈ°µÁ†ÅÊó∂Ë∞ÉÁî® go ÊñπÊ≥ïÔºåÊ†πÊçÆ state Âà§Êñ≠ÊòØÂê¶Â∫îÁî® active Ê†∑Âºè
                pages.push(<li onClick = { this.go.bind(this,i) } className = { pageCurr === i ? 'active' : "" } key={i}>{i}</li>)
            }
            pages.push(<li onClick = { this.goNext.bind(this) } className = { this.state.pageCurr === totalPage? 'nomore':"" } key={totalPage + 1}>‰∏ã‰∏ÄÈ°µ</li>)
        }else{
            pages.push(<li className = { this.state.pageCurr === 1? 'nomore':"" } key={ 0 } onClick = { this.goPrev.bind(this) }>‰∏ä‰∏ÄÈ°µ</li>)
            for(let i = startPage;i < groupCount + startPage; i++){
                if(i <= totalPage - 2){
                    pages.push(<li className = { this.state.pageCurr === i? 'active':""} key={i} onClick = { this.go.bind(this,i) }>{i}</li>)
                }
            
            }
            // ÂàÜÈ°µ‰∏≠Èó¥ÁöÑÁúÅÁï•Âè∑
            if(totalPage - startPage >= 9){
                pages.push(<li className = 'ellipsis' key={ -1 }>¬∑¬∑¬∑</li>)
            }
            // ÂÄíÊï∞Á¨¨‰∏Ä„ÄÅÁ¨¨‰∫åÈ°µ
            pages.push(<li className = { this.state.pageCurr === totalPage -1 ? 'active':""} key={ totalPage - 1 } onClick = { this.go.bind(this,totalPage - 1) }>{ totalPage -1 }</li>)
            pages.push(<li className = { this.state.pageCurr === totalPage ? 'active':""} key={ totalPage } onClick = { this.go.bind(this,totalPage) }>{ totalPage }</li>)
            // ‰∏ã‰∏ÄÈ°µ
            pages.push(<li className = { this.state.pageCurr === totalPage ? 'nomore':"" } key={ totalPage + 1 } onClick = { this.goNext.bind(this) }>‰∏ã‰∏ÄÈ°µ</li>)
        }
        return pages;
    }

    //‰º†pageÁªôparent
    sendPage=()=>{
        this.props.changePage(this.state.pageCurr)
    }

    // Êõ¥Êñ∞ state
    go(pageCurr){
        const {
            groupCount
        } = this.state;

        const {
            totalPage
        } = this.props.config

        if(pageCurr % groupCount === 1){
            this.setState({
                startPage:pageCurr
            })
        }
    
        // Â§ÑÁêÜ‰∏ä‰∏ÄÈ°µÁöÑÊÉÖÂÜµ
        if(pageCurr % groupCount === 0){
            this.setState({
                startPage:pageCurr - groupCount + 1
            })
        }

        // ÁÇπÂáªÊúÄÂêé‰∏§È°µÊó∂ÈáçÊñ∞ËÆ°ÁÆó startPage
        if(totalPage - pageCurr < 2){
            this.setState({
                startPage:totalPage - groupCount,
            })
        }
        this.setState({
            pageCurr
        });
        setTimeout(this.sendPage,1);
    }

        // È°µÈù¢ÂêëÂâç
    goPrev(){
        let {
            pageCurr,
        } = this.state;

        if(--pageCurr === 0){
            return;
        }
    
        this.go(pageCurr)
    }
    // È°µÈù¢ÂêëÂêé
    goNext(){
        let {
            pageCurr,
        } = this.state;
    
        const {
            totalPage,
        } = this.props.config;
    
        if(++pageCurr > totalPage){
            return;
        }
        this.go(pageCurr)
    }

    render(){
        const Pages = this.create.bind(this)();
        return(
            <div className='main'>
                <nav aria-label="Page navigation example">
                    <ul className = 'page pagination pagination-lg justify-content-center'>
                        { Pages }
                    </ul>
                </nav>
            </div>
        );
    }
}